import React, { useState } from 'react';
import { Button, TextField, Typography, Box } from '@mui/material';

import ReviewImage from './ReviewImage';

function UploadImages() {
    const [storeName, setStoreName] = useState('');
    const [storeFrontPicName, setStoreFrontPicName] = useState('');
    const [multiplePicsNames, setMultiplePicsNames] = useState([]);
    
    const [isPopupOpen, setIsPopupOpen] = useState(false);

    const handleNameChange = (event) => {
        setStoreName(event.target.value);
    };

    const handleStoreFrontPicChange = (event) => {
        const file = event.target.files[0];
        if (file) {
            setStoreFrontPicName(file.name);
        }
    };

    const handleMultiplePicsChange = (event) => {
        const files = event.target.files;
        if (files) {
            const fileNames = Array.from(files).map(file => file.name);
            setMultiplePicsNames(fileNames);
        }
    };

    const handleSubmit = (event) => {
        event.preventDefault();
        // Form submission logic here
        console.log(storeName, storeFrontPicName, multiplePicsNames);
        setIsPopupOpen(true); // Open the popup after form submission
    };

    const handleClosePopup = () => {
        setIsPopupOpen(false); // Close the popup
    };

    const isFormValid = () => {
        return storeName && storeFrontPicName && multiplePicsNames.length > 0;
    };

    return (
        <Box
            component="form"
            onSubmit={handleSubmit}
            noValidate
            sx={{ mt: 1 }}
        >
            <Typography variant="h6">Store Information</Typography>

            <TextField
                margin="normal"
                required
                fullWidth
                id="store-name"
                label="Store Name"
                name="storeName"
                autoComplete="off"
                autoFocus
                value={storeName}
                onChange={handleNameChange}
            />

            <Typography variant="body1" sx={{ mt: 1}}>
                Upload Store Front Pic
            </Typography>
            <Button
                variant="contained"
                component="label"
                fullWidth
                sx={{ mt: 1 }}
            >
                Upload Image
                <input
                    type="file"
                    hidden
                    onChange={handleStoreFrontPicChange}
                />
            </Button>
            {storeFrontPicName && (
                <Box sx={{ mt: 2 }}>
                    <Typography variant="subtitle1">{storeFrontPicName}</Typography>
                </Box>
            )}

            <Typography variant="body1" sx={{ mt: 3 }}>
                Upload Products Images
            </Typography>
            <Button
                variant="contained"
                component="label"
                fullWidth
                sx={{ mt: 1 }}
            >
                Upload Images
                <input
                    type="file"
                    hidden
                    multiple
                    onChange={handleMultiplePicsChange}
                />
            </Button>
            {multiplePicsNames.length > 0 && (
                <Box sx={{ mt: 2 }}>
                    <Typography variant="subtitle1">Uploaded files:</Typography>
                    <ul>
                        {multiplePicsNames.map((fileName, index) => (
                            <li key={index}>{fileName}</li>
                        ))}
                    </ul>
                </Box>
            )}

            <Button
                type="submit"
                fullWidth
                variant="contained"
                sx={{ mt: 3, mb: 2 }}
                disabled={!isFormValid()} // Disable the button if the form is not valid

            >
                Submit
            </Button>
            <ReviewImage open={isPopupOpen} onClose={handleClosePopup} />
        </Box>
        
    );
}

export default UploadImages
